@import url('https://fonts.googleapis.com/css2?family=Cairo:wght@300;400;600;700;800&family=IBM+Plex+Sans+Arabic:wght@300;400;500;600;700&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
    --background: #121212;
    --background-paper: #1A1A1A;
    --background-subtle: #222222;
    --primary: #FF5F00;
    --primary-hover: #E65100;
    --accent: #D32F2F;
    --text-primary: #FFFFFF;
    --text-secondary: #A1A1A6;
    --text-muted: #666666;
    --border-default: #333333;
    --border-active: #FF5F00;
    --radius: 0;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    direction: rtl;
}

body {
    font-family: 'IBM Plex Sans Arabic', sans-serif;
    background-color: var(--background);
    color: var(--text-primary);
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    min-height: 100vh;
}

h1, h2, h3, h4, h5, h6 {
    font-family: 'Cairo', sans-serif;
    font-weight: 800;
    letter-spacing: -0.02em;
}

/* Custom scrollbar */
::-webkit-scrollbar {
    width: 8px;
    height: 8px;
}

::-webkit-scrollbar-track {
    background: var(--background);
}

::-webkit-scrollbar-thumb {
    background: var(--border-default);
    border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
    background: var(--primary);
}

/* Selection */
::selection {
    background: var(--primary);
    color: white;
}

/* Links */
a {
    color: inherit;
    text-decoration: none;
}

/* Buttons reset */
button {
    font-family: 'IBM Plex Sans Arabic', sans-serif;
    cursor: pointer;
}

/* Input RTL support */
input, textarea, select {
    font-family: 'IBM Plex Sans Arabic', sans-serif;
    text-align: right;
}

input::placeholder,
textarea::placeholder {
    color: var(--text-muted);
}

/* Custom focus states */
*:focus-visible {
    outline: 2px solid var(--primary);
    outline-offset: 2px;
}

/* Animations */
@keyframes fadeIn {
    from { opacity: 0; transform: translateY(10px); }
    to { opacity: 1; transform: translateY(0); }
}

@keyframes pulse-glow {
    0%, 100% { box-shadow: 0 0 20px rgba(255, 95, 0, 0.3); }
    50% { box-shadow: 0 0 40px rgba(255, 95, 0, 0.5); }
}

.animate-fadeIn {
    animation: fadeIn 0.3s ease-out;
}

.animate-pulse-glow {
    animation: pulse-glow 2s ease-in-out infinite;
}

/* Glassmorphism utility */
.glass {
    background: rgba(0, 0, 0, 0.4);
    backdrop-filter: blur(12px);
    -webkit-backdrop-filter: blur(12px);
    border: 1px solid rgba(255, 255, 255, 0.1);
}

/* Gradient overlay */
.gradient-overlay {
    background: radial-gradient(circle at center, rgba(255, 95, 0, 0.05) 0%, transparent 70%);
}

/* Product card hover */
.product-card {
    transition: all 0.3s ease;
}

.product-card:hover {
    border-color: var(--primary);
    transform: translateY(-4px);
}

/* Status badges */
.status-available {
    background: rgba(34, 197, 94, 0.2);
    color: #22c55e;
    border: 1px solid rgba(34, 197, 94, 0.3);
}

.status-out-of-stock {
    background: rgba(239, 68, 68, 0.2);
    color: #ef4444;
    border: 1px solid rgba(239, 68, 68, 0.3);
}

/* Loading skeleton */
.skeleton {
    background: linear-gradient(90deg, #1A1A1A 25%, #222222 50%, #1A1A1A 75%);
    background-size: 200% 100%;
    animation: skeleton-loading 1.5s infinite;
}

@keyframes skeleton-loading {
    0% { background-position: 200% 0; }
    100% { background-position: -200% 0; }
}

/* Noise texture overlay */
.noise-overlay::before {
    content: '';
    position: absolute;
    inset: 0;
    background-image: url("data:image/svg+xml,%3Csvg viewBox='0 0 256 256' xmlns='http://www.w3.org/2000/svg'%3E%3Cfilter id='noise'%3E%3CfeTurbulence type='fractalNoise' baseFrequency='0.65' numOctaves='3' stitchTiles='stitch'/%3E%3C/filter%3E%3Crect width='100%25' height='100%25' filter='url(%23noise)'/%3E%3C/svg%3E");
    opacity: 0.03;
    pointer-events: none;
}

@layer base {
    * {
        @apply border-[#333333];
    }
    body {
        @apply bg-[#121212] text-white;
    }
}

@layer components {
    .btn-primary {
        @apply bg-[#FF5F00] hover:bg-[#E65100] text-white font-bold px-6 py-3 transition-all duration-300 hover:tracking-wider;
    }
    
    .btn-secondary {
        @apply bg-transparent border border-[#333333] hover:border-[#FF5F00] text-white px-6 py-3 transition-all duration-300;
    }
    
    .input-field {
        @apply bg-[#0A0A0A] border border-[#333333] focus:border-[#FF5F00] text-white px-4 py-3 w-full transition-colors duration-200;
    }
    
    .card {
        @apply bg-[#1A1A1A] border border-[#333333];
    }
    
    .text-silver {
        @apply text-[#A1A1A6];
    }
}
